//    -- action shamel functionhaei ast ke be request vasl mishavad

import {RequestApi as request} from '../api' // objecti shamel apiha
import M from 'materialize-css'
// action marbut be login
export const SignIn = (data) => {

    // tabdil dadeha be json mored niaz samt server
    const jsonData = JSON.stringify({
        phoneNumber: data.loginPhonenumber,
        password: data.loginPassword
    });

    // ersal etellat be samt server va dispatch khoroji be samt reducer
    return async (dispatch, getState) => {
        // ersal data be method marbut be api signin
        await request.SignIn(jsonData)
            .then(response => {
                // dispatch response be samt reducer zamani ke response ok bood
                dispatch({
                    type: 'SIGN_IN_SUCCESS',
                    response: response
                })
                // disaptch kardan zamni error
            }, error =>{
                M.toast({html : ""+error.response.data.error})
            }).catch(error => {
                M.toast({html : 'Network has error! try again ...'})
            })
    }
}