//    -- action shamel functionhaei ast ke be request vasl mishavad
import $ from 'jquery';
import M from 'materialize-css';
import {RequestApi as request} from "../api"; // objecti shamel apiha
import {GetToken} from '../../../../services/Authentication/GetToken'

const FILE_CODE = "clinic_img_1254";

// action marbout be singup
// formdata => vorodi form value
export const SignUp = (formData) => {

    //  tabdil dadeha vorudi be json mored niaz server
    const JsonData = JSON.stringify({
        phoneNumber: formData.phoneNumber,
        accountType: 2
    })

    // send data be server tavasot request
    return async (dispatch, getState) => {
        // zadan darkhast be signup action api
        await request.SignUp(JsonData)
            .then(response => {
                // dispatch kardan repsonse be reducer
                dispatch({
                    type: 'SIGN_UP_SUCCESS',
                    response: response.data
                })
            }, error => {
                M.toast({html : ""+error.response.data.error})
            }).catch(error => {
                M.toast({html : "Network has error ! try again ..."})
            })
    }
}


// check verify code hengam register
// voroudi => data form
export const VerifyCode = (data) => {

    // daryaft 4 input value verify code be yek reshteh
    var code = data.code_1 + data.code_2 + data.code_3 + data.code_4;


    // sakht data json baraye ferestadan be api
    const JsonData = JSON.stringify({
        phoneNumber: data.phoneNumber,
        code: code
    })


    //  ersal data be api va dispatch action marbute
    return async (dispatch, getState) => {
        await request.VerifyCode(JsonData)
            .then(response => {
                // dispatch response be reducer
                dispatch({
                    type: "VERIFY_SUCCESS",
                    token: response.data.token
                })
            } , error => {
                M.toast({html : ""+error.response.data.error})
            }).catch(error => {
                M.toast({html : "Network has error ! try again ..."})
            })
    }
}



// sabt data avalie fullname va password doctor
// vorodi => data form
export const SetData = (data) => {
    // tabdil form data be json
    const JsonData = JSON.stringify({
        fullName: data.fullname,
        password: data.password
    })


    // ersal be samt server va dispatch kardan khoroji az api
    return async (dispatch, getState) => {
        await request.UserFirstData(JsonData)
            .then(response => {
                // response ferestadeh shode az api
                dispatch({
                    type: 'SET_DATA_SUCCESS',
                    response: response
                })
            } , error => {
                M.toast({html : ""+error.response.data.error})
            }).catch(error => {
                M.toast({html : "Network has error ! try again ..."})
            })
    }
}


// sabt etelat takmili assistant
// vorodi => form data
export const FitData = (data) => {
    // tabdil dadeha be json
    let JsonData = {};
    if (data.isAssistance) {
        JsonData = JSON.stringify({
            isAssistant: true,
            assistant: {
                phoneNumber: data.phoneNumber,
                description: data.description,
                attachment: data.attachment
            }
        })
    } else {
        JsonData = JSON.stringify({
            isAssistant: false,
            attachment: data.attachment
        })
    }

    const jsons = JSON.stringify({
        isAssistant: true,
        assistant: {
            phoneNumber: data.phoneNumber,
            description: data.description,
            attachment: data.attachment
        }
    })
    console.log("json prev : ", jsons);
    console.log("json defualt : ", data);
    console.log("json : ", JsonData);
    return async (dispatch, getState) => {
        // ersal data be method marbut be api doctor
        await request.DoctorAdd(JsonData)
            .then(response => {
                // dispatch kardan response bargashti
                console.log("respo : ", response)
                dispatch({
                    type: 'UPLOADED_OK',
                    response: response
                })
            }, error => {
                M.toast({html : ""+error.response.data.error})
            }).catch(error => {
                M.toast({html : "Network has error ! try again ..."})
            })
    }
}


// sabt etelat takmili assistant
// vorodi => form data
export const FitDataDoctor = (data) => {
    // tabdil dadeha be json
    const files = data.filePath ? data.filePath : '';
    const JsonData = JSON.stringify({
        isAssistant: data.isAssistant,
        educationDegree: '',
        educationField: '',
        needAssistant: true,
        attachment: [files],
        grade: 0,
        commissionRate: 0,
        assistant: {
            phoneNumber: '',
            description: '',
            attachment: [],
            doctorsId: []
        },
    })

    return async (dispatch, getState) => {
        // ersal data be method marbut be api doctor
        await request.DoctorAdd(JsonData)
            .then(response => {
                localStorage.removeItem(FILE_CODE)
                // dispatch kardan response bargashti
                dispatch({
                    type: 'UPLOADED_OK',
                    response: response
                })
            }, error => {
                M.toast({html : ""+error.response.data.error})
            }).catch(error => {
                M.toast({html : "Network has error ! try again ..."})
            })
    }
}

// sabt etelat takmili assistant
// vorodi => form data
export const FitDataAssistant = (data) => {
    // tabdil dadeha be json

    const files = data.attachment ? data.attachment : '';
    const phoneNumber = data.doctors.dataUser.phoneNumber ? data.doctors.dataUser.phoneNumber : '';
    const description = data.doctors.dataUser.description ? data.doctors.dataUser.description : '';
    const JsonData = JSON.stringify({
        isAssistant: data.doctors.dataUser.isAssistant,
        needAssistant: false,
        assistant: {
            phoneNumber: phoneNumber,
            description: description,
            doctorsId: data.doctors.doctors,
            attachment: [files],
        },

    })

    return async (dispatch, getState) => {
        // ersal data be method marbut be api doctor
        console.log("dataaaaa : ", JSON.parse(JsonData));
        await request.AssistantAdd(JSON.parse(JsonData))
            .then(response => {
                // dispatch kardan response bargashti
                dispatch({
                    type: 'UPLOADED_OK_ASSIS',
                    responseAssis: response
                })
            }, error => {
                M.toast({html: "" + error.response.data.error})
            }).catch(error => {
                M.toast({html: "Network has error ! try again ..."})
            })
    }
}


// upload kardan file becdn
// voroudi => input file
export const UploadedFile = (file) => {
    // console.log("files : " , file);
    $("#check_phone_num").fadeIn();
    //dispatch karadan request
    // estefade az async await baray asyncro kardan darkhast
    return async (dispatch, getState) => {
        localStorage.setItem(FILE_CODE, "");
        // darkhast zadan be object request.uploaded va etesal be api
        await request.Uploaded(file).then(
            (response) => {
                // if response ok bood
                M.toast({html: "Your Document is uploaded"});
                $("#loading_back").fadeOut();
                localStorage.setItem(FILE_CODE, JSON.stringify(response));
                // if response eror bod
            } , error => {
                M.toast({html : ""+error.response.data.error})
            }
        ).catch(error => {
            M.toast({html : "Network has error! try again ..."})
        });
    };
};
  