//    -- safhe asli login va register mibashad
//    -- dar in safhe form login - register va verfiy code register piadeh sazi shodeh ast
//    -- yek file action va yek reducer be in file attach shode ast
import React, { useEffect, memo, useState } from "react";
// library estefadeh shode baray link dadan var routing
import { Link, withRouter } from "react-router-dom";
// library estefadeh shode baray traslation
import { useTranslation } from "react-i18next";
// actionhai signin va signup => baray tabadol etelaat az ui be server ijad shodeand
// signin baray login
import { SignIn } from "../../../entity/Redux/Auth/SignIn/Action";
//signup baray sabtnam
import { SignUp } from "../../../entity/Redux/Auth/SignUp/Action";
// baray test code verify ersali bad az sabt nam
import { VerifyCode } from "../../../entity/Redux/Auth/SignUp/Action";
// library jquery
import $ from "jquery";
// library materilize bary estefade az componenthai materilize
import M from "materialize-css";
// import {GetToken} from '../../../services/Authentication/GetToken';
// code token , secret ke be soorat sabet taein shodeand
import { TOKEN_KEY, SECRET_KEY ,ACCOUTN_TYPE } from "../../../services/Authentication/Types";
// dispatch , use selector functional bary etesalat action va reducer
import { useDispatch, connect, useSelector } from "react-redux";
// encrip kardan code token
import CryptoJs from "crypto-js";

// functional component asli signin
export default memo((props) => {
  // daryafit state az reducer ke tavasot store zakhireh shode as
  const LoginRes = useSelector((state) => state.Auth.res); // result login
  const Token = useSelector((state) => state.Auth.token); // daryaft token bad az login
  const LogRes = useSelector((state) => state.Auth.resLog); // result signup
  console.log("login res : " , LoginRes , " /\n login Token : " , Token , " /\n  Log Res : " , LogRes)
  // bad az ejray kamel component ejre mishavad
  // pishpardazandeh codehai jquery va materilize ast
  useEffect(() => {
    {
      /*
            -- verify code response
            -- check response verify code bad az daryaft va diaptch shodan action
        */
    }

    if (LoginRes) {
      $("#repass_").css({ left: "-100%" }); // namayesh boxi ke shamel inputhai verify code mishavad
      localStorage.setItem("verifyCode", LoginRes.data); // set kardan verifycode dar localstorage baray chack kardan
      M.toast({ html: "verify code : " + LoginRes.data }); // namayesh be soorat toast response
    }
    // check kardan login boodan
    if (LogRes) {
      if (LogRes.status == 200) {
        if ((LogRes.statusText = "OK")) {
          console.log("data : ", loginData.loginPhonenumber);
          console.log("props : ", LogRes.data);
          localStorage.setItem(TOKEN_KEY, LogRes.data.token); // setkardan token bad az ok boodan login
          localStorage.setItem(ACCOUTN_TYPE, LogRes.data.accountType); // setkardan token bad az ok boodan login
          M.toast({ html: "please wait to redirect panel ..." }); // redirect kardan be panel
          if (LogRes.data.accountType == 2) {
            setTimeout(()=>{
              window.location.pathname = "/doctor";
            } , 1000)
          }else if (LogRes.data.accountType == 4) {
            setTimeout(()=>{
              window.location.pathname = "/admin-top";
            } , 1000)
          }
          else if (LogRes.data.accountType == 3) {
            setTimeout(()=>{
              window.location.pathname = "/admin";
            } , 1000)
          }

        } else {
          M.toast({ html: "Your login information is incorrect!" }); // toast kardan error baray zamani ke etelat eshtebah bood
        }
      } else {
        console.log("error : ", LogRes);
      }
    }

    // end verify code response

    {
      /*
            -- token check
            -- response hasel az ersal form register yek token mideh & ma on token ro inja barrasi mikonaim
        */
    }
    if (Token) {
      // const enc_token = CryptoJs.AES.encrypt(JSON.stringify(LoginRes.accessToken), SECRET_KEY).toString();
      // const bytes = CryptoJs.AES.decrypt(enc_token, SECRET_KEY);
      // const decrypted = JSON.parse(bytes.toString(CryptoJs.enc.Utf8));
      // const token = decrypted.token;
      localStorage.setItem(TOKEN_KEY, Token);
      window.location.pathname = "/your-details"; // enteghal be component sabt detail
    }
    // end token check

    // materilize code baray tabha
    var elems = document.querySelectorAll(".tabs");
    M.Tabs.init(elems);
    // mateirlize code baray select option
    var tabh = document.querySelectorAll("select");
    M.FormSelect.init(tabh);

    {
      /*

            -- verify form jquery
            -- daryafr 4 ragham verify be sorat jodagane
        */
    }

    var body = $("#wrapper");

    // check kardan daryaft number dar inputhai verify code , paresh be input badi
    function goToNextInput(e) {
      var key = e.which,
        t = $(e.target),
        sib = t.next("input");
      if (key != 9 && (key < 48 || key > 57)) {
        e.preventDefault();
        return false;
      }
      if (key === 9) {
        return true;
      }
      if (!sib || !sib.length) {
        sib = body.find("input").eq(0);
      }
      sib.select().focus();
    }

    // check kardan vard shodan number baray raftan be input badi
    function onKeyDown(e) {
      var key = e.which;

      if (key === 9 || (key >= 48 && key <= 57)) {
        return true;
      }

      e.preventDefault();
      return false;
    }

    // check kardan focus in input
    function onFocus(e) {
      $(e.target).select();
    }

    // transaction hai input
    body.on("keyup", "input", goToNextInput);
    body.on("keydown", "input", onKeyDown);
    body.on("click", "input", onFocus);

    //   end verify form jquery
  });
  const dispatch = useDispatch();
  const { t, i18n } = useTranslation();

  // state baray sabt code verify
  const [codeVerify, setCodeVerify] = useState(null);

  // function baray daryaft va sabt verify inputha da state
  function verifyCodeGet(e) {
    setCodeVerify({
      ...codeVerify,
      [e.target.name]: e.target.value,
    });
  }

  //   end verify inputs

  // state baray sabt dadeh login dar hengam change input
  const [loginData, setLoginData] = useState(null);
  // state baray sabt dadeh register dar hengam change input
  const [registerData, setRegisterData] = useState({
    phoneNumber: null,
    userType: 2,
  });

  {
    /*
    -- login inpust
    -- daryaft va set kardan etelaad login form dar state
    */
  }

  function inputLoginChange(e) {
    setLoginData({
      ...loginData,
      [e.target.name]: e.target.value,
    });
  }

  // end login inputs

  {
    /*
    -- register inpust
    -- daryaft va set kardan etelaad register form dar state
    */
  }

  function inputRegisterChange(e) {
    var phoneNumber = $("input[name=phoneNumber]").val();
    const regexs = /^(?:\+?86)?1(?:3\d{3}|5[^4\D]\d{2}|8\d{3}|7(?:[0-35-9]\d{2}|4(?:0\d|1[0-2]|9\d))|9[0-35-9]\d{2}|6[2567]\d{2}|4[579]\d{2})\d{6}$/;
    localStorage.setItem("rols", 2); // daryaft role dar hengam register baray redirect nahaey be panel
    // sabt dadehai register dar state
    setRegisterData({
      ...registerData,
      phoneNumber: phoneNumber,
      userType: 2,
    });
  }

  // end register inputs

  {
    /*
    -- signup
    -- dispat kardan function marboot be signuo
    */
  }

  function verify_code() {
    const regexs = /^(?:\+?86)?1(?:3\d{3}|5[^4\D]\d{2}|8\d{3}|7(?:[0-35-9]\d{2}|4(?:0\d|1[0-2]|9\d))|9[0-35-9]\d{2}|6[2567]\d{2}|4(?:(?:10|4[01])\d{3}|[68]\d{4}|[579]\d{2}))\d{6}$/;
    var phoneNumber = registerData.phoneNumber;
    // check kardan khali naboodan inputhai signuo
    if (regexs.test(phoneNumber)) {
      $("#loading_back").fadeIn();
      setCodeVerify({
        ...codeVerify,
        phoneNumber: registerData.phoneNumber,
      });

      setTimeout(function () {
        $("#loading_back").fadeOut();
        // dispatch kardan action signup baray daryaft code verify
        dispatch(SignUp(registerData));
      }, 3000);
    } else {
      M.toast({ html: "Please enter the phone number correctly" });
    }
  }

  // end signup

  {
    /*
    -- signin check
    -- dispatch kardan form login baray daryaft token
    */
  }

  function redirectToDash(e) {
    e.preventDefault();
    dispatch(SignIn(loginData));
  }

  // end signin check

  {
    /*
    -- language
    -- do zabaneh kardan site
    */
  }

  function handleLang(e) {
    // e.preventDefault();
    // sabt type language dar storage
    if (!localStorage.getItem("lan")) {
      localStorage.setItem("lan", "en");
    }
    //daryaft type language as localstorage
    var lan = localStorage.getItem("lan");
    var ckeckbox = $("input[name=cahngelan]");
    if (ckeckbox.is(":checked") && lan === "en") {
      localStorage.setItem("lan", "ch");
    } else {
      localStorage.setItem("lan", "en");
    }
    let lang = localStorage.getItem("lan");
    // gharar dadan language dar library translate
    i18n.changeLanguage(lang);
  }

  // end language

  {
    /*
    -- verify dispatch
    -- dispatch kardan action VerifyCode baraye check , barrasi verify code ersali karbar
    */
  }

  function handleCheckFormVerify(e) {
    e.preventDefault();
    dispatch(VerifyCode(codeVerify));
  }

  // end dispatch

  const select_type = t("select-type.l");
  return (
    <div className="auth_back">
      <div id="back_shado"></div>

      <div style={{ width: "31rem" }} id="auth_box">
        {/* input change language ba checkbox */}
        <div id="change_or_sub">
          <input
            type="checkbox"
            name="cahngelan"
            onChange={handleLang}
            id="change_lang_login"
          />
          <span>{t("Lang.l")}</span>
          {/*<span></span>*/}
        </div>

        <div className={`mini_box_over`}>
          <div id={`mini_circule`}>
            <i className="material-icons">person_outline</i>
          </div>
        </div>
        <div className={`mini_box_over`} style={{ right: 0 }}>
          <div id={`mini_circule_r`}>
            <img src="/img/icons/doctorB.png" />
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="row">
              <div className="input-field col s12" style={{ marginTop: 55 }}>
                <p id="text_sub">{t("Welcome-to.l")}</p>
                <p
                  id="text_sub"
                  style={{
                    fontSize: "40px",
                    marginTop: "15px !important",
                    marginBottom: "15px !important",
                  }}
                >
                  Bi Con
                </p>
                <p id="text_sub">{t("Backend-panel.l")}</p>
              </div>
            </div>

            <div className="register_type_user" id="verifyBorder">
              <div id="loading_back">
                <div
                  className="preloader-wrapper big active"
                  id="loading_progress"
                >
                  <div className="spinner-layer spinner-blue-only">
                    <div className="circle-clipper left">
                      <div className="circle"></div>
                    </div>
                    <div className="gap-patch">
                      <div className="circle"></div>
                    </div>
                    <div className="circle-clipper right">
                      <div className="circle"></div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="doctor_form">
                <ul id="tabs-swipe-demo" className="tabs">
                  <li className="tab col s6">
                    <a className="active" href="#test-swipe-1">
                      {t("Login.l")}
                    </a>
                  </li>
                  <li className="tab col s6">
                    <a href="#test-swipe-2">{t("Register.l")}</a>
                  </li>
                </ul>
                <div id="test-swipe-1">
                  {/*
                                        -- login form
                                        -- form marboot be login dar tab login
                                        */}

                  <form
                    id="login"
                    onSubmit={(e) => redirectToDash(e)}
                    className="col s12"
                    style={{
                      paddingLeft: 15,
                      paddingRight: 15,
                      paddingTop: 5,
                      paddingBottom: 10,
                    }}
                  >
                    <div
                      className="row"
                      style={{ marginBottom: "none !important" }}
                    >
                      <div
                        className="input-field col s12"
                        style={{ marginTop: 0 }}
                      >
                        <p style={{ color: "#ccc" }}>
                          &nbsp;&nbsp;{t("Phone-num.l")}
                        </p>
                        <div id="input_back">
                          <input
                            onChange={inputLoginChange}
                            lang="zh"
                            placeholder={t("U-phone-num.l")}
                            autoComplete="false"
                            name="loginPhonenumber"
                            required
                            type="text"
                            className="validate"
                          />
                        </div>
                        <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>

                        {/*<label htmlFor="password">Password</label>*/}
                      </div>
                    </div>
                    <div
                      className="row"
                      style={{ marginBottom: "none !important" }}
                    >
                      <div
                        className="input-field col s12"
                        style={{ marginTop: 0 }}
                      >
                        <p style={{ color: "#ccc" }}>
                          &nbsp;&nbsp;{t("Password.l")}
                        </p>
                        <div id="input_back">
                          <input
                            placeholder={t("UPassword.l")}
                            autoComplete="false"
                            name="loginPassword"
                            onChange={inputLoginChange}
                            required
                            type="password"
                            className="validate"
                          />
                        </div>
                        <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>
                        {/*<label htmlFor="password">Password</label>*/}
                      </div>
                    </div>

                    <div className={`col s12`}>
                      <div className={`row`}>
                        <div
                          className={`col s6`}
                          style={{ marginTop: 20, marginBottom: 20 }}
                        >
                          <button
                            type="submit"
                            className="btn blue darken-4 waves-effect"
                          >
                            {t("SignIn.l")}
                          </button>
                        </div>
                      </div>
                      <div className="col s12" style={{ padding: 0 }}>
                        <span style={{ float: "left", color: "#fff" }}>
                          {t("Forget.l")}
                        </span>
                        <Link to="/repass">
                          <span id="forget_pass">{t("Reset.l")}</span>
                        </Link>
                      </div>
                    </div>
                  </form>

                  {/*    end login form */}
                </div>
                <div id="test-swipe-2" style={{ height: "220px" }}>
                  <div id="repass_">
                    <div className="container">
                      <div className="row">
                        {/*
                                                    -- get phone
                                                    -- input marbut be phone number baray register
                                                    */}

                        <div className="col s6">
                          <div
                            className="input-field col s12"
                            style={{ marginTop: 0 }}
                          >
                            <p style={{ color: "#ccc" }}>
                              &nbsp;&nbsp;{t("Phone-num.l")}
                            </p>
                            <div id="input_back">
                              <input
                                placeholder={t("U-phone-num.l")}
                                autoComplete="false"
                                name="phoneNumber"
                                min="0"
                                max="9"
                                onChange={(e) => inputRegisterChange(e)}
                                required
                                type="text"
                                className="validate"
                              />
                            </div>
                            <p id="alert-p">&nbsp;&nbsp;{t("must-field.l")}</p>
                            {/*<label htmlFor="password">Password</label>*/}
                          </div>
                          <div className={`col s6`}>
                            {/*<Link to="/login/verify">*/}
                            <a
                              className="btn blue darken-4 waves-effect"
                              onClick={verify_code}
                              style={{ textTransform: "unset !important" }}
                              id="check_phone_num"
                            >
                              {t("SignUp.l")}
                            </a>
                            {/*</Link>*/}
                          </div>
                        </div>

                        {/*  end get phone  */}

                        {/*
                                                    -- get verify code
                                                    -- daray 4 input baray 4 ragham code verify
                                                */}
                        <form onSubmit={(e) => handleCheckFormVerify(e)}>
                          <div className="col s6">
                            <div
                              className="input-field col s12"
                              style={{ marginTop: 0 }}
                            >
                              <p style={{ color: "#ccc" }}>
                                &nbsp;&nbsp;{t("Verify.l")}
                              </p>

                              <div id="wrapper">
                                <div id="dialog">
                                  <div id="form">
                                    <input
                                      type="text"
                                      placeholder="--"
                                      maxLength="1"
                                      size="1"
                                      name="code_1"
                                      onChange={(e) => verifyCodeGet(e)}
                                      min="0"
                                      max="9"
                                      pattern="[0-9]{1}"
                                    />
                                    <input
                                      type="text"
                                      placeholder="--"
                                      maxLength="1"
                                      size="1"
                                      onChange={(e) => verifyCodeGet(e)}
                                      name="code_2"
                                      min="0"
                                      max="9"
                                      pattern="[0-9]{1}"
                                    />
                                    <input
                                      type="text"
                                      placeholder="--"
                                      maxLength="1"
                                      size="1"
                                      onChange={(e) => verifyCodeGet(e)}
                                      name="code_3"
                                      min="0"
                                      max="9"
                                      pattern="[0-9]{1}"
                                    />
                                    <input
                                      type="text"
                                      placeholder="--"
                                      maxLength="1"
                                      size="1"
                                      name="code_4"
                                      min="0"
                                      max="9"
                                      onChange={(e) => verifyCodeGet(e)}
                                      pattern="[0-9]{1}"
                                    />
                                  </div>
                                </div>
                              </div>

                              <p id="alert-p">
                                &nbsp;&nbsp;{t("must-field.l")}
                              </p>

                              {/*<label htmlFor="password">Password</label>*/}
                            </div>

                            <div className={`col s6`}>
                              {/*<NavLink to={{pathname : '/login/verify' }}>*/}
                              <button
                                type="submit"
                                style={{ textTransform: "unset !important" }}
                                id="check_code"
                                className="btn blue darken-4 waves-effect"
                              >
                                {t("check.l")}
                              </button>
                              {/*</NavLink>*/}
                            </div>
                          </div>
                        </form>

                        {/*    end get verify code */}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
